  brwsr.Date_parse = (!brwsr.ff)? Date.parse : function(str){
    var time = Date.parse(str);
    if (isNaN(time)) {
      var idx = str.lastIndexOf('.');
      time = Date.parse(str.substr(0,idx)) + parseInt(str.substr(idx+1,3));
    }
    return time;
  };
