      var nw = pn.naturalWidth;
      var nh = pn.naturalHeight;
      var fw = (nw>w)? nw/w : 1;
      var fh = (nh>h)? nh/h : 1;
//      if (fh>1 || fw>1) {
        pn.setAttribute('width', (fw>=fh)? w : nw/fh);
        pn.setAttribute('height',(fh>=fw)? h : nh/fw);
