      for (var j=0;j<keys.length;j++) {
        var i = keys[j];
        d_str += i+':'+((Array.isArray(thq[i]))? thq[i] : (thq[i].backlinks || thq[i].remove_if_no_backlinks || 'P'))+', ';
      }
      return d_str;
