      bottom_top: function(pn){
        var s = pn.style;
        if (s.bottom) {
          s.top = bottom2top(s.bottom,pn) + 'px';
          s.bottom = null;
        }
        if (s.right) {
          s.left = right2left(s.right,pn) + 'px';
          s.right = null;
        }
      },
