////          qout:'"', // 4chan
////          '#039':"'", // 4chan
////          '#044':',', // 4chan
////          '#44' :',', // 4chan
////        };
////        function com2txt(match,p1,offset,string){
////          if (match[0]==='&') {
////            var retval = com2txt_subs[p1];
